import React from "react";
import "../../css/Darkmode/Darkmode.css";
import { RiMoonClearFill } from "react-icons/all";
import "../../css/Dashboard/Dashboard.css";

class DarkModeToggle extends React.Component {
  constructor(props) {
    super(props);

    if (JSON.parse(localStorage.getItem("DARK_MODE")) === true) {
      document.body.classList.add("dark-mode");
    }

    this.state = {
      darkMode: JSON.parse(localStorage.getItem("DARK_MODE")),
    };

    this.handleModeChange = this.handleModeChange.bind(this);
  }

  handleModeChange() {
    if (!this.state.darkMode) {
      document.body.classList.add("dark-mode");
    } else {
      document.body.classList.remove("dark-mode");
    }

    this.setState({
      darkMode: !this.state.darkMode,
    });
    localStorage.setItem("DARK_MODE", !this.state.darkMode);
  }

  render() {
    return <button onClick={this.handleModeChange}>Change Mode</button>;
  }
}

export default DarkModeToggle;

